import { apiGet, apiPost } from "./api"; //引入第一次封装的文件

//登录
export function login(data: any) {
  return new Promise((resolve, reject) => {
    apiPost("index.php/index/index/login", data)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
