//删除
export function del(id: any) {
  return new Promise((resolve, reject) => {
    apiPost("index.php/index/User/delWxUser?id=", id)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
